<template>
   <v-row>
        <v-col
            cols="12" 
        >
            <v-card class="pa-6 green">
                <v-card-title class="white--text">Above</v-card-title>
                <v-row no-gutters class="pb-6"  v-if="fiveAboveETFFilter != 0">
                    <v-col
                    v-for="(stock, j) in fiveAboveETFFilter"
                    :key="j"
                    cols="4"
                    xs="4"
                    sm="4"
                    md="4"
                    lg="4"
                    >
                        <v-card
                            class="pa-2"
                            outlined
                            tile
                            @click="goToStockLookup(stock[0])"
                            :class="{ 'blue white--text': stock[1].entry == 'STRONG LONG ENTRY', 'green white--text': stock[1].entry == 'Strong Uptrend', 'blue yellow--text': stock[1].entry == 'STRONG LONG ENTRY' && stock[1].k == 100 && stock[1].d == 100 }"
                            >
                            {{ stock[0] }}: {{ stock[1].price | toCurrency() }}
                        </v-card>
                    </v-col>
                </v-row>

                <v-row no-gutters>
                    <v-col
                        v-for="(stock, i) in fifteenCrossFilter"
                        :key="i"
                        cols="3"
                        xs="3"
                        sm="3"
                        md="3"
                        lg="3"
                    >
                        <v-card
                            class="pa-2"
                            outlined
                            tile
                            @click="goToStockLookup(stock[0])"
                            :class="{ 'blue white--text': stock[1].entry == 'STRONG LONG ENTRY', 'green white--text': stock[1].entry == 'Strong Uptrend', 'blue yellow--text': stock[1].entry == 'STRONG LONG ENTRY' && stock[1].k == 100 && stock[1].d == 100 }"
                            >
                            {{ stock[0] }}: {{ stock[1].price | toCurrency() }}
                        </v-card>
                    </v-col>

                </v-row>          
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
export default {
    name: 'fiveAboveList',
    filters: {
        toCurrency(amount) {
        var formatter = new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
        });
        return formatter.format(amount)
        },    
    },
    mounted() {
    },
    computed: {
        fifteenCrossFilter() { 
            const fifteen_thirty_list = []
            for (const stock in this.$store.state.all_five) {
                const fifteen_stock_data = this.$store.state.all_five[stock]
                if (stock === 'VXX' || stock === 'QQQ' || stock === 'SPY') {
                    continue
                }
                if (fifteen_stock_data.cloud_position === 'Above') {
                    fifteen_thirty_list.push([stock, fifteen_stock_data])
                    // fifteen_thirty_list[stock] = { fifteen_stock_data }
                } 
            }
            return fifteen_thirty_list
        },
        fiveAboveETFFilter() { 
            const etf_list = []           
            for (const stock in this.$store.state.all_five) {
                if (stock === 'VXX' || stock === 'QQQ' || stock === 'SPY') {
                    const five_stock_data = this.$store.state.all_five[stock]
                    if (five_stock_data.cloud_position === 'Above') {
                        etf_list.push([stock, five_stock_data])
                    }
                }
            }
            return etf_list
        },
    },
    methods: {
        goToStockLookup(stock){
            this.$router.push({ name: 'Stock', params: { id: stock } })
        },
        // fiveAboveETFFilter() { 
        //     const etf_list = []           
        //     for (const stock in this.$store.state.all_five) {
        //         if (stock === 'VXX' || stock === 'QQQ' || stock === 'SPY') {
        //             const five_stock_data = this.$store.state.all_five[stock]
        //             if (five_stock_data.cloud_position === 'Above') {
        //                 etf_list.push([stock, five_stock_data])
        //             }
        //         }
        //     }
        //     console.log(etf_list)
        //     return etf_list
        // },
    }
}
</script>

