                                            </v-row>
                                            <v-card-text class="primary white--text">
                                                <v-row justify="space-around" class="py-0">
                                                    <v-card-subtitle class="text-center">RSI:</v-card-subtitle>
                                                    <v-card-subtitle class="text-center">{{ alert.stock_alert.alert_data.rsi }}</v-card-subtitle>
                                                </v-row>
                                                <v-row justify="space-around" class="py-0" :class="{ 'yellow--text' : alert.stock_alert.alert_data.k == 0 && alert.stock_alert.alert_data.d == 0}">
                                                    <v-card-subtitle class="text-center">stochK:</v-card-subtitle>
                                                    <v-card-subtitle class="text-center">{{ alert.stock_alert.alert_data.k }}</v-card-subtitle>
                                                    <v-card-subtitle class="text-center">stochD:</v-card-subtitle>
                                                    <v-card-subtitle class="text-center">{{ alert.stock_alert.alert_data.d }}</v-card-subtitle>
                                                </v-row>
                                            </v-card-text>
                                            <v-card-text>
                                                <v-row justify="space-around" class="py-0" >
                                                    <v-card-subtitle class="text-center">Date:</v-card-subtitle>
                                                    <v-card-subtitle class="text-center"> {{ alert.stock_alert.datestamp }}</v-card-subtitle>
                                                    <v-card-subtitle class="text-center">Time:</v-card-subtitle>
                                                    <v-card-subtitle class="text-center"> {{ alert.stock_alert.timestamp }}</v-card-subtitle>
                                                </v-row>
                                            </v-card-text>
                                            <v-card-text class="primary white--text">
                                            <v-row justify="space-around" class="py-0">
                                                <v-card-subtitle class="text-center">CrossOver Alert:</v-card-subtitle>
                                                <v-card-subtitle class="text-center">{{ alert.stock_alert.alert_data.crossover_event }}</v-card-subtitle>
                                            </v-row>
                                            </v-card-text>
                                        </v-card>
                                    </v-row>
                                </v-card>
                            </v-slide-item>
                        </v-slide-group>
                    </v-sheet>                   
