<script>

export default {
    name: 'Alerts',
     data () {
      return {
        model: null,
      }
    },
    computed: {
        pastAboveAlerts() {
            const above_list = {}
            for (const stock in this.$store.state.alerts) {
                const stock_data = this.$store.state.alerts[stock]
                // if (stock_data.cloud_position === 'Above' && stock_data.entry === 'STRONG LONG ENTRY') {
                above_list[stock] = stock_data
                // }
            }
            console.log(above_list)
            return above_list
            // return this.$store.state.alerts
        }
    },
    mounted() {
    },
    filters: {
        toCurrency(amount) {
        var formatter = new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
        });
        return formatter.format(amount)
        },    
    },
    methods: {
        goToStockLookup(stock){
            this.$router.push({ name: 'Stock', params: { id: stock } })
        },
       
    }


}
</script>

